import { graphql, useStaticQuery } from "gatsby"
import React, { useState } from "react"
import { FaMailBulk } from "react-icons/fa"
import { FiInstagram } from "react-icons/fi"
import { Col, Container, Row } from "reactstrap"
import { SiteMetadata } from "src/util"
import { MailSignupModal } from "../mailing-list"
import styles from "./standard-footer.module.scss"
import { OutboundLink, event } from "react-ga"

type Data = {
  site: { siteMetadata: SiteMetadata }
}

const MailSignupButton = () => {
  const [isOpen, setOpen] = useState(false)
  return (
    <>
      <a className={styles.icons} onClick={() => setOpen(true)}>
        <FaMailBulk title="Mailing List" />
      </a>
      <MailSignupModal
        isOpen={isOpen}
        setOpen={setOpen}
        source="Footer button"
      />
    </>
  )
}

type StandardFooterProps = {
  pathname: string
}

/**
 * A shared page footer that is used on basically every page.
 */
export const StandardFooter = () => {
  const data: Data = useStaticQuery(graphql`
    query Footer {
      site {
        siteMetadata {
          instagramUrl
        }
      }
    }
  `)

  return (
    <footer>
      <Container className={styles.footer} tag="nav">
        <Row>
          <Col className={styles.socialMedia}>
            <h5>Follow us</h5>
            <p className={styles.icons}>
              <OutboundLink
                to={data.site.siteMetadata.instagramUrl}
                eventLabel="Instagram (from footer)"
              >
                <FiInstagram title="Instagram" />
              </OutboundLink>{" "}
              <MailSignupButton />
            </p>
          </Col>
          <Col>
            <p>
              <a href="/about">About</a> | <a href="/privacy">Privacy Policy</a>{" "}
              | <a href="/contact">Contact Us</a>
            </p>
            <p>
              Copyright © 2020 Abolitionist Action Central Coast SLO. All rights
              reserved.
            </p>
          </Col>
        </Row>
      </Container>
    </footer>
  )
}
