import addToMailchimp from "gatsby-plugin-mailchimp"
import React, { FC, useState, useEffect } from "react"
import { exception, event } from "react-ga"
import {
  Button,
  Col,
  Form,
  Input,
  InputGroup,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Row,
} from "reactstrap"
import { SiteMetadata } from "src/util"

type Data = {
  site: { siteMetadata: SiteMetadata }
}

type Result = {
  msg: string
  result: "error" | "success"
}

function sleep(ms: number) {
  return new Promise(resolve => setTimeout(resolve, ms))
}

const MailForm: FC<{
  toggle: (wasSuccess?: boolean) => void
  source: string
}> = ({ toggle, source }) => {
  const pathname = ""

  const [first, setFirst] = useState("")
  const onChangeFirst = (ev: React.ChangeEvent<HTMLInputElement>) => {
    setFirst(ev.target.value)
  }
  const [last, setLast] = useState("")
  const onChangeLast = (ev: React.ChangeEvent<HTMLInputElement>) => {
    setLast(ev.target.value)
  }
  const [email, setEmail] = useState("")
  const onChangeEmail = (ev: React.ChangeEvent<HTMLInputElement>) => {
    setEmail(ev.target.value)
  }
  const [status, setStatus] = useState(null as Result | null)
  const [disabled, setDisabled] = useState(false)

  const onSubmit = async () => {
    setDisabled(true)
    try {
      const result = await addToMailchimp(
        email,
        {
          PATHNAME: pathname,
          FNAME: first,
          LNAME: last,
        },
        undefined
      )

      setStatus(result)
      if (result.result == "error") {
        setDisabled(false)
        exception({
          description: "Mailing list signup error: " + result.status.msg,
          fatal: false,
        })
        return
      }

      event({
        category: "Mailing List",
        action: "Signed up successfully",
        label: "Modal",
      })
      await sleep(1000)
      toggle(true)
    } catch (e) {
      setDisabled(false)
      setStatus({
        msg: "Error attempting to subscribe to mailing list.",
        result: "error",
      })
    }
  }

  useEffect(() => {
    event({
      category: "Mailing List",
      action: "Opened modal",
      label: source,
    })
  }, [])

  return (
    <>
      <Form onSubmit={onSubmit}>
        <ModalBody>
          <Row>
            <Col>
              <InputGroup>
                <Input
                  onChange={onChangeFirst}
                  placeholder="First Name"
                  disabled={disabled}
                />
              </InputGroup>
            </Col>
            <Col>
              <InputGroup>
                <Input
                  onChange={onChangeLast}
                  placeholder="Last Name"
                  disabled={disabled}
                />
              </InputGroup>
            </Col>
          </Row>
          <Row>
            <Col>
              <InputGroup>
                <Input
                  onChange={onChangeEmail}
                  placeholder="Email address"
                  disabled={disabled}
                />
              </InputGroup>
            </Col>
          </Row>
          {status ? (
            <Col
              className={
                status.result == "error" ? "text-danger" : "text-success"
              }
              dangerouslySetInnerHTML={{ __html: status.msg }}
            />
          ) : null}
        </ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={onSubmit} disabled={disabled}>
            Submit!
          </Button>
        </ModalFooter>
      </Form>
    </>
  )
}

type MailSignupModalProps = {
  isOpen: boolean
  setOpen: (open: boolean) => void
  source: string
}

export const MailSignupModal: FC<MailSignupModalProps> = ({
  setOpen,
  isOpen,
  source,
}) => {
  const toggleOpen = (wasSuccess?: boolean) => {
    if (isOpen && !wasSuccess) {
      event({
        category: "Mailing List",
        action: "Canceled modal",
        label: source,
      })
    }

    setOpen(!isOpen)
  }

  return (
    <Modal isOpen={isOpen} toggle={() => toggleOpen()}>
      <ModalHeader toggle={() => toggleOpen()}>
        Sign up for the mailing list!
      </ModalHeader>
      <MailForm toggle={toggleOpen} source={source} />
    </Modal>
  )
}
