import React, { FC, ReactNode } from "react"
import { CookiesProvider } from "react-cookie"
import "src/scss/custom-bootstrap.scss"
import { MainNavbar } from "./main-navbar"
import { StandardFooter } from "./standard-footer"

type StandardLayoutProps = {
  children: ReactNode
}

/**
 * A component that provides the standard navbar, footer, and content background for
 * basically every page.
 */
export const StandardLayout: FC<StandardLayoutProps> = ({ children }) => {
  return (
    <CookiesProvider>
      <div
        style={{
          zIndex: 1000,
          position: "fixed",
          top: 0,
          overflow: "hidden",
          width: "100%",
        }}
      >
        <MainNavbar />
      </div>
      <div className="main-wrapper">{children}</div>
      <StandardFooter />
    </CookiesProvider>
  )
}
