import React, { FC, useState } from "react"
import {
  Button,
  Collapse,
  Nav,
  Navbar,
  NavbarBrand,
  NavbarToggler,
  NavItem,
  NavLink,
} from "reactstrap"
import { useViewportQuery } from "src/util"
import styles from "./main-navbar.module.scss"
import { Link } from "gatsby"
import { OutboundLink } from "react-ga"

export type NavbarProps = {}

const Title: FC = () => {
  const matches = useViewportQuery("strictly")
  return <>{matches.xs ? "AACCS" : "Abolitionist Action Central Coast SLO"}</>
}

const Brand = () => {
  const matches = useViewportQuery("strictly")

  return (
    <NavbarBrand href="/" dark>
      <Title />
    </NavbarBrand>
  )
}

/**
 * A shared navbar that is used on basically every page.
 */
export const MainNavbar: FC<NavbarProps> = () => {
  const [isOpen, setOpen] = useState(false)
  const toggle = () => setOpen(!isOpen)
  const matches = useViewportQuery("strictly")

  return (
    <Navbar className={styles.navbar} color="dark" dark expand="md">
      <Brand />
      <NavbarToggler onClick={toggle} />
      <Collapse isOpen={isOpen} navbar>
        <Nav navbar className="ml-auto mr-auto">
          <NavItem>
            <NavLink href="/about">About</NavLink>
          </NavItem>
          <NavItem>
            <NavLink href="/events">Events</NavLink>
          </NavItem>
          <NavItem>
            <NavLink href="/policy/demands">Demands</NavLink>
          </NavItem>
          <NavItem style={{ paddingRight: 10 }}>
            <Button
              tag={OutboundLink}
              color="success"
              eventName="Sign up form (navbar)"
              to="https://docs.google.com/forms/d/e/1FAIpQLSfxemYLnW3qYbNSF4EBICIL8VAzx4E-PtIJAuEI8DdRNH0wqg/viewform"
              target="_blank"
            >
              Get Involved
            </Button>
          </NavItem>
          <NavItem>
            <Button tag={Link} to="/take-action" color="danger">
              Take action!
            </Button>
          </NavItem>
        </Nav>
      </Collapse>
    </Navbar>
  )
}
