import { useMedia } from "react-media"

export const VIEWPORT_QUERIES_STRICT = {
  xs: "(min-width: 0px) and (max-width: 575px)",
  sm: "(min-width: 576px) and (max-width: 767px)",
  md: "(min-width: 768px) and (max-width: 991px)",
  lg: "(min-width: 992px) and (min-width: 1199px)",
  xl: "(min-width: 1200px)",
}

export const VIEWPORT_QUERIES_AT_LEAST = {
  xs: "(min-width: 0px)",
  sm: "(min-width: 576px)",
  md: "(min-width: 768px)",
  lg: "(min-width: 992px)",
  xl: "(min-width: 1200px)",
}

export function useViewportQuery(mode: "strictly" | "at least") {
  var query: any
  switch (mode) {
    case "strictly":
      return useMedia({ queries: VIEWPORT_QUERIES_STRICT })
    case "at least":
      return useMedia({ queries: VIEWPORT_QUERIES_AT_LEAST })
  }
}
